<template>
    <div>
      <Loader v-if="loading"/>
      <div v-else>
        <div class="breadcrumb-wrap">
          <router-link to="/history" class="breadcrumb">{{'History' | localize}}</router-link>
          <a @click.prevent class="breadcrumb">
            {{record.typeText}}
          </a>
        </div>
        <div class="row">
          <div class="col s12 m6">
            <div class="card" :class="record.typeClass">
              <div class="card-content white-text">
                <p>{{'Description' | localize}}: {{record.description}}</p>
                <p>{{'Sum' | localize}}: {{record.amount | currency}}</p>
                <p>{{'Category' | localize}}: {{record.categoryName}}</p>
    
                <small>{{record.date | date}}</small>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

</template>

<script>
export default {
  data() {
    return {
      loading: true,
      record: null
    }
  },
  metaInfo: {
    title: 'Detail | CRM'
  },
  async mounted() {
    const id = this.$route.params.id
    const record = await this.$store.dispatch('fetchRecordById', id)
    const category = await this.$store.dispatch('fetchCategoryById', record.recordId)

    this.record = {
      ...record,
      categoryName: category.title,
      typeClass: record.type === 'income' ? 'green' : 'red',
      typeText: record.type === 'income' ? 'Доход' : 'Расход'
    }
    this.loading = false
  }
}
</script>